import styled from 'styled-components/macro';

export const Container = styled.div`
  display: grid;
  grid-template-columns: 256px 1fr;
  column-gap: 48px;
  width: 100%;
  max-width: 1040px;
  margin: 0 auto;
  padding-top: 80px;
  padding-right: 10px;
  padding-left: 10px;
  padding-bottom: 40px;
`;
